import { Editor } from '@tiptap/core';
import StarterKit from '@tiptap/starter-kit';
import Heading from '@tiptap/extension-heading';
import TextAlign from '@tiptap/extension-text-align';
import Image from '@tiptap/extension-image';
import Color from '@tiptap/extension-color';
import TextStyle from '@tiptap/extension-text-style'; 

document.addEventListener('DOMContentLoaded', () => {
  const editorElement = document.querySelector('#editor');
  const formElement = document.querySelector('form');

  if (editorElement) {
    const editor = new Editor({
      element: editorElement,
      extensions: [
        StarterKit.configure({
          heading: {
            levels: [1, 2, 3, 4, 5, 6], // Define the heading levels you want to support
          },
        }),
        Heading.configure({
          levels: [1, 2, 3, 4, 5, 6], // Add multiple heading levels
        }),
        TextAlign.configure({
          types: ['heading', 'paragraph'], // Allow text alignment for headings and paragraphs
        }),
        Image, // Allow image insertion
        TextStyle, // Allow text styles (used for custom font sizes)
        Color.configure({ types: ['textStyle'] }), // Allow color changes
      ],
      content: '',
      onUpdate({ editor }) {
        const postBody = document.getElementById('postBody');
        postBody.value = editor.getHTML(); // Store editor content in a hidden field
      },
    });

    // Toolbar Button Event Listeners
    document.getElementById('bold-btn').addEventListener('click', () => {
      editor.chain().focus().toggleBold().run();
    });

    document.getElementById('italic-btn').addEventListener('click', () => {
      editor.chain().focus().toggleItalic().run();
    });

    document.getElementById('heading-btn').addEventListener('click', () => {
      editor.chain().focus().toggleHeading({ level: 2 }).run();
    });

    document.getElementById('center-btn').addEventListener('click', () => {
      editor.chain().focus().setTextAlign('center').run();
    });

    document.getElementById('left-btn').addEventListener('click', () => {
      editor.chain().focus().setTextAlign('left').run();
    });

    document.getElementById('right-btn').addEventListener('click', () => {
      editor.chain().focus().setTextAlign('right').run();
    });

    document.getElementById('font-size-btn').addEventListener('click', () => {
      editor.chain().focus().setFontSize('24px').run();
    });

    document.getElementById('color-btn').addEventListener('click', () => {
      editor.chain().focus().setColor('#FF0000').run();
    });

    document.getElementById('image-btn').addEventListener('click', () => {
      const imageUrl = prompt('Enter image URL');
      if (imageUrl) {
        editor.chain().focus().setImage({ src: imageUrl }).run();
      }
    });

    formElement.addEventListener('submit', (event) => {
      event.preventDefault();
      const postBody = document.getElementById('postBody');
      postBody.value = editor.getHTML();
      formElement.submit();
    });
  }
});